@use '../../global-variables' as gv;

.card-holder {
  width: 30vw;
  height: 80vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

.map-counter {
  font-size: 1em;
  font-weight: 700;
}

.map-number-of-trees {
  display: flex;
  flex-direction: column;
  justify-content: right;
}

.trees-planted-label {
  margin: 0;
}

.dashboard-header {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  width: 30vw;
}

.dashboard-outline {
  border: 0.35vw solid #2d2d2d;
  border-radius: 1vw;
  width: 30vw;
  height: 80vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.planted-count {
  margin: 0.25em 0;
  font-weight: bold;
  font-size: 1.25em;
}

@media (max-width: gv.$default-resize-width) {
  .dashboard-outline {
    width: 80vw;
  }

  .card-holder {
    width: 100%;
    height: 100%;
  }

  .dashboard-header {
    width: 100%;
  }
}